.table-container table {
    border-collapse: collapse;
    max-width: 90%;
    position: relative;
    left: 100px;
    text-align: center;
}

.table-container th,
.table-container td {
    border: 2px solid #000000;
    padding: 10px;
}

.table-container th {
    background-color: #ff000000;
    font-weight: 600;
}