body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

.wrapper {
    height: 10000px;
    width: 100%;
    background-image: url(fon.jpg);
    background-size: 1920px 1080px;
    background-repeat: repeat;
}

form {
    margin-left: 100px;
    margin-top: 50px;
    margin-bottom: 30px;
    width: 600px;
    border: 1px;
}

div {
    height: 35px;
}

span {
    text-decoration: underline;
}

.header {
    font-family: 'Times New Roman', Times, serif;
    font-size: 250%;
    text-align: center;
    text-shadow: 1px -3px 7px rgb(61, 56, 223);
}

input#name {
    margin-left: 26px;
}

input#capacity {
    margin-left: 9px;
}

input#price {
    margin-left: 18px;
}

button {
    margin-left: 150px;
    margin-top: 5px;
}

button:focus,
input:focus {
    outline: 1px, solid, black;
}

img {
    border: 6px solid rgb(71, 108, 255);
    border-style: ridge;
    width: 320px;
    height: 240px;
    position: relative;
    top: 0px;
    left: 100px;
}

img:hover {
    transform: scale(1.4);
}

.description1 {
    position: absolute;
    left: 450px;
    top: 100px;
    color: rgb(0, 0, 0);
}

.description2 {
    position: absolute;
    left: 450px;
    top: 200px;
    color: rgb(0, 0, 0);
}

.description3 {
    position: absolute;
    left: 450px;
    top: 300px;
    color: rgb(0, 0, 0);
}